.currentAffairsWrapOne {
  width: 500px;
  height: 400px;
  position: relative;
  z-index: 1000;
  border: 1px solid red;
  background: #000000;
}
.currentAffairsWrapOne .headerOne {
  width: 100%;
}
.currentAffairsWrapOne .contentOne {
  margin-top: 7px;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne .headerTitleOne {
  width: 70px;
  text-align: center;
  border: 1px solid red;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne .headerTitleOne h5 {
  font-size: 7px;
  color: #FFFFFF;
  font-weight: normal;
}
.currentAffairsWrapOne .contentOne .contentHeaderOne .headerTitleOne span {
  font-size: 12px;
  color: #FA6767;
  border: 1px solid white;
  margin: 0;
}
